'use client'
import Link from "next/link";
import { Card, CardContent, CardFooter } from "../components/ui/card";
import { Button } from "../components/ui/button";
import { AspectRatio } from "../components/ui/aspect-ratio";
import { useFeaturedProducts } from "../hooks/useWordPress";
import { Skeleton } from "../components/ui/skeleton";
import { Star, Heart, ShoppingCart, Eye, Sparkles } from "lucide-react";

const FeaturedProducts = () => {
  const { data: products, isLoading, error } = useFeaturedProducts(12);

  // Placeholder products for loading state
  const placeholderProducts = Array(12).fill(null);

  return (
    <section className="py-4 bg-gradient-to-br from-slate-50 via-white to-purple-50">
      <div className="mx-auto px-4">
        {/* Header Section */}
        <div className="text-center mb-12">
          <div className="inline-flex items-center gap-2 bg-gradient-to-r from-purple-100 to-pink-100 px-4 py-2 rounded-full mb-4">
            <Sparkles className="h-4 w-4 text-purple-600" />
            <span className="text-sm font-medium text-purple-700">Most Popular</span>
          </div>
          <h2 className="text-4xl md:text-5xl font-bold bg-gradient-to-r from-purple-600 via-pink-600 to-blue-600 bg-clip-text text-transparent mb-4">
            Popular Decoration Packages
          </h2>
          <p className="text-gray-600 max-w-2xl mx-auto text-lg">
            Our most loved decoration packages chosen by thousands of happy customers
          </p>
        </div>
        
        {error ? (
          <div className="text-center py-16">
            <div className="bg-red-50 border border-red-200 rounded-2xl p-8 max-w-md mx-auto">
              <div className="text-red-500 text-2xl mb-2">😔</div>
              <p className="text-lg text-red-600 mt-2">Failed to load products</p>
              <p className="text-red-500 text-sm">Please try again later</p>
            </div>
          </div>
        ) : (
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 md:gap-6">
            {isLoading
              ? placeholderProducts.map((_, index) => (
                  <div key={`loading-${index}`} className="h-full">
                    <Card className="overflow-hidden border-0 shadow-lg bg-white/80 backdrop-blur-sm h-full flex flex-col">
                      <AspectRatio ratio={1}>
                        <Skeleton className="h-full w-full rounded-t-xl" />
                      </AspectRatio>
                      <CardContent className="p-3 md:p-4 flex-1">
                        <Skeleton className="h-3 w-16 mb-2 rounded-full" />
                        <Skeleton className="h-4 w-full mb-2 rounded" />
                        <Skeleton className="h-4 w-3/4 mb-3 rounded" />
                        <div className="flex justify-between items-center">
                          <Skeleton className="h-5 w-16 rounded-full" />
                          <Skeleton className="h-4 w-12 rounded-full" />
                        </div>
                      </CardContent>
                      <CardFooter className="p-3 md:p-4 pt-0 mt-auto">
                        <Skeleton className="h-9 w-full rounded-full" />
                      </CardFooter>
                    </Card>
                  </div>
                ))
              : products?.map((product, index) => (
                  <div key={product.id} className="animate-fade-in-up h-full" style={{ animationDelay: `${index * 0.1}s` }}>
                    <Link href={`/product/${product.slug}`} className="block group h-full">
                      <Card className="overflow-hidden border-0 shadow-lg hover:shadow-2xl transition-all duration-500 h-full cursor-pointer bg-white/90 backdrop-blur-sm group-hover:scale-105 group-hover:-translate-y-2 flex flex-col">
                        {/* Image Container with Overlay */}
                        <div className="relative overflow-hidden">
                          <AspectRatio ratio={1}>
                            <img 
                              src={product.images[0]?.src || "https://images.unsplash.com/photo-1533174072545-7a4b6ad7a6c3?auto=format&fit=crop&w=500&q=80"}
                              alt={product.name}
                              className="object-cover w-full h-full transition-all duration-500 group-hover:scale-110"
                            />
                          </AspectRatio>
                          
                          {/* Gradient Overlay */}
                          <div className="absolute inset-0 bg-gradient-to-t from-black/20 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                          
                          {/* Quick Actions */}
                          <div className="absolute top-2 right-2 flex flex-col gap-1 opacity-0 group-hover:opacity-100 transition-all duration-300 transform translate-x-2 group-hover:translate-x-0">
                            <button className="p-2 bg-white/90 backdrop-blur-sm rounded-full shadow-lg hover:bg-white transition-colors">
                              <Heart className="h-3 w-3 text-gray-600 hover:text-red-500" />
                            </button>
                            <button className="p-2 bg-white/90 backdrop-blur-sm rounded-full shadow-lg hover:bg-white transition-colors">
                              <Eye className="h-3 w-3 text-gray-600" />
                            </button>
                          </div>
                          
                          {/* Category Badge */}
                          <div className="absolute top-2 left-2">
                            <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-white/90 backdrop-blur-sm text-gray-700 shadow-sm">
                              {product.categories[0]?.name || "Decoration"}
                            </span>
                          </div>
                        </div>

                        <CardContent className="p-3 md:p-4 flex-1">
                          <h3 className="font-bold text-sm md:text-base line-clamp-2 text-gray-800 group-hover:text-purple-600 transition-colors mb-2 min-h-[2.5rem]">
                            {product.name}
                          </h3>
                          
                          <div className="flex justify-between items-center mt-auto">
                            <div className="flex flex-col">
                              <span className="font-bold text-lg text-purple-600">
                                ₹{Number(product.price).toFixed(0)}
                              </span>
                              {product.regular_price && product.regular_price !== product.price && (
                                <span className="text-xs text-gray-500 line-through">
                                  ₹{Number(product.regular_price).toFixed(0)}
                                </span>
                              )}
                            </div>
                            
                            <div className="flex items-center gap-1 bg-yellow-50 px-2 py-1 rounded-full">
                              <Star className="h-3 w-3 text-yellow-500 fill-current" />
                              <span className="text-xs font-medium text-yellow-700">
                                {product.average_rating || "5.0"}
                              </span>
                            </div>
                          </div>
                        </CardContent>

                        <CardFooter className="p-3 md:p-4 pt-0 mt-auto">
                          <Button className="w-full bg-purple-600 hover:bg-purple-700 text-white border-0 rounded-full font-medium transition-all duration-300 group-hover:shadow-lg">
                            <ShoppingCart className="h-4 w-4 mr-2" />
                            View Details
                          </Button>
                        </CardFooter>
                      </Card>
                    </Link>
                  </div>
                ))}
          </div>
        )}
        
        {/* View All Button */}
        <div className="mt-12 text-center">
          <Link href="/products">
            <Button size="lg" className="bg-purple-600 hover:bg-purple-700 text-white px-8 py-3 rounded-full font-bold text-lg shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105">
              <Sparkles className="h-5 w-5 mr-2" />
              View All Products
            </Button>
          </Link>
        </div>
      </div>

      <style jsx>{`
        @keyframes fade-in-up {
          from {
            opacity: 0;
            transform: translateY(30px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        
        .animate-fade-in-up {
          animation: fade-in-up 0.6s ease-out forwards;
          opacity: 0;
        }
      `}</style>
    </section>
  );
};

export default FeaturedProducts;